@tailwind base;
@tailwind components;
@tailwind utilities;

/* width */
body::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    /* box-shadow: inset 0 0 5px grey;  */
    /* border-radius: 10px; */
    background: white;
    border: none;
    
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background:black;
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background:black; 
  }